/*******************************************************************************
 * Частицы
 * Particles
 */

export default (canvas)=>{

  const ctx = canvas.getContext('2d')
  let w = canvas.width=canvas.offsetWidth
  let h = canvas.height=canvas.offsetHeight
  const particles = []
  const properties = {
    bgColor: 'rgba(0,0,0, 0.8)',
    particleColor: 'rgba(255, 240, 240, 1)',
    particleRadius: 1.5,
    particleCount: canvas.offsetWidth < 500 ? 20 : 160,
    particleMaxVelocity: 0.7,
    lineLength: 130,
    particleLife: 6
  }

  window.onresize = function(){
    w = canvas.width=canvas.offsetWidth
    h = canvas.height =canvas.offsetHeight
  }

  class Particle{
    constructor(){
      this.x = Math.random()*w
      this.y = Math.random()*h
      this.velocityX = Math.random()*(properties.particleMaxVelocity*2)-properties.particleMaxVelocity
      this.velocityY = Math.random()*(properties.particleMaxVelocity*2)-properties.particleMaxVelocity
      this.life = Math.random()*particles.particleLife*60 // 60-кол. кадров в секунду
    }
    position(){
      this.x += this.x + this.velocityX > w && this.velocityX > 0 || this.x + this.velocityX < 0 && this.velocityX < 0? this.velocityX*=-1 : this.velocityX
      this.y += this.y + this.velocityY > h && this.velocityY > 0 || this.y + this.velocityY < 0 && this.velocityY < 0? this.velocityY*=-1 : this.velocityY
    }
    reDraw(){
      ctx.beginPath()
      ctx.arc(this.x, this.y, properties.particleRadius, 0, Math.PI*2)
      ctx.closePath()
      ctx.fillStyle = properties.particleColor
      ctx.fill()
    }
    reCalculateLife(){
      if(this.life < 0.8){
        this.x = Math.random()*w
        this.y = Math.random()*h
        this.velocityX = Math.random()*(properties.particleMaxVelocity*2)-properties.particleMaxVelocity
        this.velocityY = Math.random()*(properties.particleMaxVelocity*2)-properties.particleMaxVelocity
        this.life = Math.random()*particles.particleLife*60 // 60-кол. кадров в секунду
      }
      this.life--
    }
  }

  function reDrawBackground(){
    ctx.fillStyle = properties.bgColor
    ctx.fillRect(0,0, w, h)
  }

  function drawLines(){
    let x1, y1, x2, y2, length, opacity
    for(let i in particles){
      for(let j in particles){
        x1 = particles[i].x // координата первой частицы
        y1 = particles[i].y
        x2 = particles[j].x // координата второй частицы
        y2 = particles[j].y
        length = Math.sqrt(Math.pow(x2-x1, 2) + Math.pow(y2-y1,2) )
        if(length < properties.lineLength){
          opacity = 1-length/properties.lineLength
          ctx.lineLength = '0,5'
          // ctx.strokeStyle = `rgba(0,153,255, ${opacity})`
          ctx.strokeStyle = `rgba(255,51,0, ${opacity})`
          ctx.beginPath()
          ctx.moveTo(x1, y1)
          ctx.lineTo(x2, y2)
          ctx.closePath()
          ctx.stroke()
        }
      }
    }
  }

  function reDrawParticles(){
    for(let i in particles){
      particles[i].reCalculateLife()
      particles[i].position()
      particles[i].reDraw()
    }
  }

  function loop(){
    reDrawBackground()
    reDrawParticles()
    drawLines()

    return canvas.offsetWidth < 500 ? null : requestAnimationFrame(loop)
  }

  function init(){
    for(let i=0; i<properties.particleCount; i++){
      particles.push(new Particle)
    }
    loop()
  }

  init()
}
