/*******************************************************************************
 * Притяжение и отталкивание частиц
 * Attraction and repulsion of particles
 */

export default (canvas)=>{
  const config = {
    dotMinRadius: 6,
    dotMaxRadius: 20,
    sphereRaius: 300, // радиус отталкивание
    bigDotRadius: 35, // большая точка
    mouseSize: 120,
    massFactor : 0.002,
    defColor: `rgba(250,10,30, 0.9)`,
    smooth: 0.65 // для торможения
  }

  const ctx = canvas.getContext('2d')
  let w, h, mouse, dots

  class Dot{
    constructor(r){
      this.position = {x:mouse.x, y:mouse.y}
      this.velosity = {x:0, y:0}
      this.radius = r || random( config.dotMinRadius, config.dotMaxRadius )
      this.mass = this.radius * config.massFactor
      this.color = config.defColor
    }
    draw(x, y){
      this.position.x = x || this.position.x + this.velosity.x
      this.position.y = y || this.position.y + this.velosity.y
      createCircle(this.position.x, this.position.y, this.radius, true, this.color)
      createCircle(this.position.x, this.position.y, this.radius, false, config.defColor) // контур
    }
  }

  function init(){
    w = canvas.width  = canvas.offsetWidth
    h = canvas.height = canvas.offsetHeight
    mouse = {x:w/2, y:h/2, down:false}
    dots = []

    dots.push( new Dot(config.bigDotRadius) ) //
  }

  function setPos({layerX, layerY}){
    [mouse.x, mouse.y] = [layerX, layerY]
  }

  function isDown(){
    mouse.down = !mouse.down
  }

  function createCircle(x,y,radius,isFill,color){
    ctx.fillStyle = ctx.strokeStyle = color
    ctx.beginPath()
    ctx.arc(x, y, radius, 0, Math.PI*2)
    ctx.closePath()
    isFill ? ctx.fill() : ctx.stroke()
  }

  function updateDots(){ // обновляем скорость
    for(let i=1; i<dots.length; i++){
      let acc = {x:0, y:0}
      for(let j=0; j<dots.length; j++){
        if(i==j) continue
        let [a, b] = [ dots[i], dots[j] ]

        let delta = {
          x: b.position.x - a.position.x,
          y: b.position.y - a.position.y
        }
        let dist = Math.sqrt(delta.x*delta.x + delta.y*delta.y ) || 1
        let force = (dist - config.sphereRaius) / dist * b.mass // чем больше радиус тем больше масса, притяжение сильнее

        if(j==0){
          let alpha = config.mouseSize/dist
          a.color = `rgba(250,10,30, ${alpha})`
          dist < config.mouseSize ? force = (dist-config.mouseSize) * b.mass : force = a.mass
        }

        acc.x +=delta.x * force // ускорение по х
        acc.y +=delta.y * force // ускорение по y
      }
      dots[i].velosity.x = dots[i].velosity.x * config.smooth + acc.x * dots[i].mass
      dots[i].velosity.y = dots[i].velosity.y * config.smooth + acc.y * dots[i].mass
    }
    dots.map(e => e==dots[0]? e.draw(mouse.x, mouse.y) : e.draw())
  }

  function random(min, max){
    return Math.random() * ( max-min ) + min
  }

  function reDrawBackground(){
    ctx.clearRect(0, 0, w, h)
    ctx.fillStyle = `#000`
    ctx.fillRect(0,0, w, h)
  }

  function loop(){
    reDrawBackground()

    if(mouse.down){dots.push( new Dot() )}
    updateDots()

    return canvas.offsetWidth < 500 ? null : requestAnimationFrame(loop)
  }

  init()
  loop()

  canvas.addEventListener(`mousemove`, setPos)
//  window.addEventListener(`mousedown`, isDown)
//  window.addEventListener(`mouseup`,   isDown)
  window.addEventListener(`mousemove`,   isDown)
}
